<script lang="ts" setup>
import { ToastType } from '~/lib/models/internal/ui/toast'

const toast = useToast()
const { t } = useTranslations()

const props = withDefaults(defineProps<{ dataClipboard?: string }>(), {
  dataClipboard: 'data-clipboard',
})

const emit = defineEmits<{ copied: [string] }>()

const copyToClipboard = async () => {
  const element = document.querySelector(`[data-clipboard="${props.dataClipboard}"]`)
  if (!element) return
  const text = (element as HTMLElement).innerText
  try {
    // Intent API modern
    await navigator.clipboard.writeText(text)
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
  } catch (err) {
    // Fallback IOS / Safari
    const textarea = document.createElement('textarea')
    textarea.value = text
    textarea.style.position = 'fixed' // evita scroll
    textarea.style.opacity = '0'
    document.body.appendChild(textarea)
    textarea.focus()
    textarea.select()
     
    document.execCommand('copy')
    document.body.removeChild(textarea)
  }
  toast.add({
    title: t('common.toast.clipboard.title'),
    description: t('common.toast.clipboard.description'),
    orientation: 'vertical',
    color: ToastType.INFO,
    icon: 'i-lucide-info',
  })
  emit('copied', text)
}
</script>
<template>
  <div class="flex cursor-pointer items-center text-2xl" @click="copyToClipboard">
    <UIcon name="i-lucide-copy" />
  </div>
</template>
