import * as z from 'zod'

export interface MyModalProps {
  isOpen: boolean
  title: string
  message: string
  illustration?: string
  /* highlightInfo */
  highlightInfo?: {
    title: string
    subtitle?: string
  }
  htmlDescription?: string
  /* Actions */
  actions?: {
    primary?: string
    secondary?: string
  }
  isRedeemed?: boolean
}

export const MODAL_ACTIONS = {
  PRIMARY: 'primary',
  SECONDARY: 'secondary',
} as const

export const modalActionSchema = z.enum([MODAL_ACTIONS.PRIMARY, MODAL_ACTIONS.SECONDARY])

export type ModalActionType = z.infer<typeof modalActionSchema>
