import {
  CALENDAR_SETTINGS,
  type CalendarSettings,
} from '~/lib/models/advent-calendar/advent-calendar'
import {
  MODAL_ACTIONS,
  type ModalActionType,
  type MyModalProps,
} from '~/lib/models/internal/ui/modal'
import { ToastType } from '~/lib/models/internal/ui/toast'
import type { CurrentPrize, KmpMessage } from '~/lib/models/kmp-bridge'

export const useAdventCalendarGame = () => {
  const debug = useDebug('Advent:Calendar')

  const termsStore = useTermsStore()
  const { t } = useTranslations()
  const {
    initGame,
    closeGame,
    copyCodeToClipboard,
    dataBridge,
    isRedeemSuccess,
    isErrorBidgeConextion,
  } = useKmpBridge()
  const toast = useToast()

  // --- Constants ---
  const CLAIM_DELAY = 1000
  const IMAGE_PRIZE_DEFAULT = '/images/advent-calendar/christmas-tree.svg'

  // --- Reactive state ---
  const loading = ref<boolean>(true)
  const hasAcceptedTerms = ref<boolean>(false)
  const showTerms = ref<boolean>(false)
  const showModalRedeem = ref<boolean>(false)
  const isRedeemedToday = ref<boolean>(false)
  const currentDayAward = ref<CalendarSettings | null>(null)
  const prizeData = ref<CurrentPrize | null>(null)

  // --- Calendar setup ---
  const calendarDaysConfig = ref<CalendarSettings[]>([...CALENDAR_SETTINGS])
  const todayDay = computed(() => new Date().getDate()) // Day month (1-31)
  const historicPrizes = computed(() => dataBridge.value.startResponse?.prizes ?? [])
  const redeemedDays = computed(() =>
    historicPrizes.value.map(day => parseDayFromIsoDate(day.date)),
  )

  // Build calendar days and disable past/redeemed days
  const calendarDays = computed(() =>
    calendarDaysConfig.value.map(day => {
      const isToday = day.day === todayDay.value
      const isPast = day.day < todayDay.value
      const isUsed = redeemedDays.value.includes(day.day)

      return {
        ...day,
        disabled: isPast || (isToday && isRedeemedToday.value),
        used: isUsed || (isToday && isRedeemedToday.value),
      }
    }),
  )

  // --- Dialog state ---
  const dialogState = reactive<MyModalProps>({
    isOpen: false,
    title: t('adventCalendar.mainCalendar.noPrize.title'),
    message: t('adventCalendar.mainCalendar.noPrize.description'),
    illustration: IMAGE_PRIZE_DEFAULT,
    highlightInfo: undefined,
    htmlDescription: '',
    actions: {
      primary: t('adventCalendar.actions.backToCalendar'),
    },
    isRedeemed: false,
  })

  // --- Trigger the redeem animation flow ---
  const startRedeemFlow = () => {
    debug('Start redeem flow animations')
    // Get day to calendar UI
    const dayConfig = calendarDays.value.find(day => day.day === todayDay.value)
    // Init flow redeem price
    if (dayConfig && isRedeemSuccess.value) {
      currentDayAward.value = dayConfig
      showModalRedeem.value = true
    }
  }

  // --- Display details for a redeemed (historic) prize ---
  const showModalViewPrice = (info: CalendarSettings) => {
    const viewSelectedPrize = historicPrizes.value.find(
      item => parseDayFromIsoDate(item.date) === info.day,
    )
    const prizeApplied = viewSelectedPrize?.items[0]
    const extraData = {
      code: prizeApplied?.value,
      description: prizeApplied?.promotionDetails?.description,
      image: prizeApplied?.promotionDetails?.imageUrl,
      terms: prizeApplied?.promotionDetails?.termsAndConditions,
    }
    debug('Show prize  detail redeemed:', prizeApplied)
    formatDialogPrizeData(true, extraData)
    // Open modal
    dialogState.isOpen = true
  }

  // --- Format modal dialog data for both current and redeemed prizes. ---
  const formatDialogPrizeData = (
    isRedeemed = false,
    extraData?: {
      code?: string | null
      description?: string | null
      image?: string | null
      terms?: string | null
    },
  ) => {
    dialogState.isRedeemed = isRedeemed
    // Current prize (just won)
    if (!isRedeemed) {
      const { code, promotionDetails } = prizeData.value!
      dialogState.illustration = promotionDetails?.imageUrl?.trim()
        ? promotionDetails?.imageUrl
        : IMAGE_PRIZE_DEFAULT
      dialogState.title = t('adventCalendar.mainCalendar.prize.title')
      dialogState.message = promotionDetails?.name ?? ''
      dialogState.htmlDescription = promotionDetails?.termsAndConditions ?? ''
      dialogState.highlightInfo = { title: code, subtitle: promotionDetails?.description ?? '' }
      dialogState.actions = {
        primary: t('adventCalendar.actions.applyNow'),
        secondary: t('adventCalendar.actions.applyLater'),
      }
      return
    }
    // Redeemed (historic) prize
    dialogState.illustration = extraData?.image?.trim() ? extraData.image : IMAGE_PRIZE_DEFAULT
    dialogState.title = t('adventCalendar.mainCalendar.prizeRedeemed.title')
    dialogState.message = extraData?.description ?? ''
    dialogState.htmlDescription = extraData?.terms ?? ''
    dialogState.highlightInfo = {
      title: extraData?.code ?? '',
      subtitle: extraData?.description ?? '',
    }
    dialogState.actions = { primary: t('adventCalendar.actions.backToCalendar') }
  }

  // --- Handle start response received from Kotlin bridge ---
  const handleStartResponse = (data: KmpMessage['startCurrent']) => {
    debug('Received start response:', data)
    if (data) {
      const mcId = data?.mcId
      // Set current prize
      prizeData.value = data ?? null
      if (prizeData.value) {
        formatDialogPrizeData()
      }
      if (!mcId) return
      const accepted = termsStore.hasAcceptedTerms(mcId)
      hasAcceptedTerms.value = accepted
      loading.value = false
      if (accepted) {
        // if accept show flow calendar
        setTimeout(() => {
          startRedeemFlow()
        }, CLAIM_DELAY)
      } else {
        // Show intro
        showTerms.value = true
      }
    }
  }

  // --- Handle successful prize redemption ---
  const handleRedeemSuccess = () => {
    // Close modal flow redeem
    showModalRedeem.value = false
    if (isRedeemSuccess.value) {
      isRedeemedToday.value = true
      // Delay dialog to sync with animation
      setTimeout(() => {
        dialogState.isOpen = true
      }, 400)
    }
  }

  // --- Handle dialog button actions (apply/back) ---
  const handleActionDialog = (action: ModalActionType) => {
    const isPrimary = action === MODAL_ACTIONS.PRIMARY
    const shouldCloseGame = isPrimary && isRedeemSuccess.value
    if (shouldCloseGame) {
      // Close game / return to APP
      returnToApp()
    } else {
      dialogState.isOpen = false
    }
    // Close dialog / return calendar view
    isRedeemSuccess.value = false
  }

  // --- Copy prize code to clipboard ---
  const handleCopyCode = () => {
    copyCodeToClipboard()
    closeGame()
  }

  // --- Handle acceptance of terms and conditions ---
  const handleAcceptTerms = () => {
    const mcId = prizeData.value?.mcId
    if (!mcId) return
    debug('User accept terms & conditions:', mcId)
    termsStore.acceptTerms(mcId)
    hasAcceptedTerms.value = true
    showTerms.value = false
    // Flow redeem
    setTimeout(() => {
      startRedeemFlow()
    }, CLAIM_DELAY)
  }

  // --- Return to APP / close game
  const returnToApp = () => {
    closeGame()
  }

  // --- Lifecycle ---
  onMounted(() => {
    initGame()
  })

  watchEffect(() => {
    if (dataBridge.value.startCurrent) handleStartResponse(dataBridge.value.startCurrent)
  })

  watchEffect(() => {
    if (isErrorBidgeConextion.value && loading.value) {
      loading.value = false
      toast.add({
        title: t('common.toast.connectionBridge.title'),
        description: t('common.toast.connectionBridge.description'),
        orientation: 'vertical',
        color: ToastType.ERROR,
        icon: 'i-lucide-info',
      })
    }
  })

  // --- Expose composable API ---
  return {
    // state
    loading,
    hasAcceptedTerms,
    showTerms,
    showModalRedeem,
    showModalViewPrice,
    isRedeemedToday,
    currentDayAward,
    calendarDays,
    dialogState,
    // Actions
    startRedeemFlow,
    handleRedeemSuccess,
    handleActionDialog,
    handleAcceptTerms,
    handleCopyCode,
    returnToApp,
  }
}
