<script setup lang="ts">
const {
  loading,
  showTerms,
  handleAcceptTerms,
  calendarDays,
  currentDayAward,
  showModalViewPrice,
  showModalRedeem,
  handleRedeemSuccess,
  dialogState,
  handleActionDialog,
  handleCopyCode,
  returnToApp,
} = useAdventCalendarGame()
</script>
<template>
  <div class="w-full">
    <!-- Loader -->
    <Loader v-if="loading" />
    <!-- Terms & conditions -->
    <IntroCalendar v-else-if="showTerms" @accept="handleAcceptTerms" />
    <!-- Calendar adviento -->
    <CalendarMain
      v-else
      :days-config="calendarDays"
      @view-prize="showModalViewPrice"
      @return-app="returnToApp"
    />
    <!-- Flow redeem prize -->
    <RedeemFlow
      v-if="showModalRedeem"
      :day-current-award="currentDayAward"
      :is-open="showModalRedeem"
      @redeem-success="handleRedeemSuccess"
    />
    <Dialog
      v-bind="dialogState"
      @close="dialogState.isOpen = false"
      @action="handleActionDialog"
      @copied="handleCopyCode"
    />
  </div>
</template>
