import * as z from 'zod'

/**
 * Layout Enum
 */

export const LAYOUT_DAY_CARD = {
  SQUARE: 'square',
  RECTANGLE: 'rectangle',
  VERTICAL: 'vertical',
} as const

export type LayoutDayCard = (typeof LAYOUT_DAY_CARD)[keyof typeof LAYOUT_DAY_CARD]

export const LayoutDayCardEnum = z.enum(
  Object.values(LAYOUT_DAY_CARD) as [LayoutDayCard, ...LayoutDayCard[]],
)

/**
 * Background card Enum
 */

export const BACKGROUND_DAY_CARD = {
  YELLOW: 'bg-mcd-yellow-card',
  RED: 'bg-mcd-red-card',
  GREEN: 'bg-mcd-green-card',
} as const

export type BackgroundDayCard = (typeof BACKGROUND_DAY_CARD)[keyof typeof BACKGROUND_DAY_CARD]

export const BackgroundDayCardEnum = z.enum(
  Object.values(BACKGROUND_DAY_CARD) as [BackgroundDayCard, ...BackgroundDayCard[]],
)

/**
 * Calendar settings
 */

export const calendarSettingsDto = z.object({
  day: z.number().int().min(1).max(24),
  background: z.string(),
  layout: LayoutDayCardEnum,
  image: z.string(),
  disabled: z.boolean(),
  used: z.boolean(),
})

export type CalendarSettings = z.infer<typeof calendarSettingsDto>

export const calendarSettingsListView = z.array(calendarSettingsDto)

