<script setup lang="ts">
import type { CalendarSettings } from '~/lib/models/advent-calendar/advent-calendar'

const props = defineProps<{
  daysConfig: CalendarSettings[]
}>()

const itemsDays = computed(() => props.daysConfig)

const { t } = useTranslations()

const emit = defineEmits<{ 'view-prize': [payload: CalendarSettings]; 'return-app': [] }>()

const handleViewPrice = (dayConfig: CalendarSettings) => {
  emit('view-prize', dayConfig)
}
</script>
<template>
  <section class="h-screen w-full overflow-y-auto p-4">
    <!-- Header -->
    <header class="mt-2 mb-5">
      <img
        class="h-auto w-full"
        src="/images/advent-calendar/mcd-header-calendar.svg"
        alt="MCDonalds"
      />
    </header>
    <!-- Grid calendar -->
    <div class="relative grid grid-cols-4 grid-rows-8 gap-2 pb-2">
      <DayCard
        v-for="(day, index) in itemsDays"
        :key="day.day"
        :day-config="day"
        :is-last="index === itemsDays.length - 1"
        @view-prize="handleViewPrice"
      />
      <!-- Christmas lights -->
      <img
        class="absolute bottom-0 left-[-16px] h-[72px] w-auto scale-x-[-1]"
        src="/images/advent-calendar/mcd-lights.svg"
        alt="Christmas lights"
      />
      <img
        class="absolute right-[-16px] bottom-0 h-[72px] w-auto"
        src="/images/advent-calendar/mcd-lights.svg"
        alt="Christmas lights"
      />
    </div>
    <!-- Back to APP -->
    <div class="text-center text-xs">
      <ULink @click="emit('return-app')">
        {{ t('adventCalendar.actions.backToApp') }}
      </ULink>
    </div>
    <!-- Terms -->
    <div class="mt-6">
      <p class="text-[10px]">
        <span>{{ t('adventCalendar.mainCalendar.terms.description') }}&nbsp;</span>
        <ULink :to="t('adventCalendar.mainCalendar.terms.visit.link')">
          {{ t('adventCalendar.mainCalendar.terms.visit.label') }}
        </ULink>
      </p>
    </div>
  </section>
</template>
