<script lang="ts" setup>
import {
  CARD_IMAGES,
  LAYOUT_DAY_CARD,
  type CalendarSettings,
} from '~/lib/models/advent-calendar/advent-calendar'
// Subcomponentes
import DayCardRectangle from './DayCardRectangle.vue'
import DayCardVertical from './DayCardVertical.vue'
import DayCardSquare from './DayCardSquare.vue'

const props = defineProps<{
  dayConfig: CalendarSettings
  isLast?: boolean
  isOpen?: boolean
}>()

const { t } = useTranslations()

const classAppliedLayout = computed(() => {
  const layoutClass = {
    [LAYOUT_DAY_CARD.SQUARE]: 'col-span-1',
    [LAYOUT_DAY_CARD.RECTANGLE]: 'col-span-2',
    [LAYOUT_DAY_CARD.VERTICAL]: 'col-span-1 row-span-2',
  }
  return layoutClass[props.dayConfig.layout] || 'col-span-1'
})

const isPastDay = computed(() => props.dayConfig.disabled)

const isRedeemed = computed(() => props.dayConfig.used)

const iconRedeemed = computed(() => (isRedeemed.value ? CARD_IMAGES.UNLOCKED : CARD_IMAGES.LOCKED))

const emit = defineEmits<{ 'view-prize': [payload: CalendarSettings] }>()

const layoutComponent = computed(() => {
  switch (props.dayConfig.layout) {
    case LAYOUT_DAY_CARD.RECTANGLE:
      return DayCardRectangle
    case LAYOUT_DAY_CARD.VERTICAL:
      return DayCardVertical
    default:
      return DayCardSquare
  }
})
</script>
<template>
  <div
    :class="[classAppliedLayout, { 'col-start-2': isLast }]"
    class="card-shadow relative z-1 h-full min-h-[76px] rounded-[3px]"
  >
    <!-- Overlay button -->
    <DayCardOverlay
      v-if="isPastDay"
      :label="t('adventCalendar.actions.viewPrize')"
      :is-redeemed="isRedeemed"
      @view-prize="emit('view-prize', dayConfig)"
    />
    <!-- Redeemed icon -->
    <div v-if="!isOpen" class="absolute top-0.5 right-0 left-0 z-[4] mx-auto max-w-6">
      <span
        :class="[dayConfig.background, isRedeemed ? 'left-[10px]' : 'right-0 left-0']"
        class="squared-lock-shadow absolute top-[1px] z-[1] mx-auto block h-[6px] w-[5px]"
      />
      <img
        class="absolute right-0 left-0 mx-auto h-auto"
        :class="[isRedeemed ? 'top-0.5 left-[-4px] w-[20px]' : 'top-1 w-[16px]']"
        :src="iconRedeemed"
        :alt="isRedeemed ? 'Image redeemed' : 'Image lock'"
      />
    </div>
    <!-- Component day dinamic  SQUARE, RECTANGLE or VERTICAL -->
    <component :is="layoutComponent" :day-config="dayConfig" :is-open="isOpen" />
    <!-- Shadow IMG card -->
    <img
      :src="CARD_IMAGES.SHADOW"
      alt="Image shadow"
      class="pointer-events-none absolute bottom-[-9px] left-0 z-[1] h-[9px] w-full select-none"
    />
  </div>
</template>
