<script lang="ts" setup>
import { DotLottieVue } from '@lottiefiles/dotlottie-vue'
const props = withDefaults(
  defineProps<{
    lottieKey: string
    loop?: boolean
    autoplay?: boolean
    width?: string
    height?: string
  }>(),
  {
    src: '',
    loop: false,
    autoplay: false,
    width: '380px',
    height: '380px',
  },
)

const playerRef = ref<
  | (InstanceType<typeof DotLottieVue> & {
      getDotLottieInstance: () => HTMLDotLottiePlayerElement | null
    })
  | null
>(null)

const emit = defineEmits<{ complete: [true] }>()

const dotLottieInstance = ref<HTMLDotLottiePlayerElement | null>(null)

const handleInternal = ref<() => void>(() => {})

const lottieSrc = `lottie/${props.lottieKey}.lottie`

const setupListener = () => {
  if (!playerRef.value) return

  dotLottieInstance.value = playerRef.value.getDotLottieInstance()
  if (!dotLottieInstance.value) return

  handleInternal.value = () => {
    if (!props.loop) {
      emit('complete', true)
    }
  }

  dotLottieInstance.value?.addEventListener('complete', handleInternal.value)
}

const removeListener = () => {
  if (dotLottieInstance.value && handleInternal) {
    dotLottieInstance.value?.removeEventListener('complete', handleInternal.value)
  }
}

onMounted(() => {
  setupListener()
})

watch(
  () => lottieSrc,
  () => {
    removeListener()
    setupListener()
  },
)

onBeforeUnmount(() => {
  removeListener()
})
</script>
<template>
  <DotLottieVue
    v-if="lottieSrc"
    ref="playerRef"
    class="m-auto"
    :style="{ height, width }"
    :autoplay="autoplay"
    :loop="loop"
    :src="lottieSrc"
  />
</template>
