import { defineStore } from 'pinia'

export const useTermsStore = defineStore('terms', {
  state: () => ({
    acceptedByUser: {} as Record<string, boolean>,
  }),
  actions: {
    acceptTerms(mcId: string) {
      this.acceptedByUser[mcId] = true
    },
    hasAcceptedTerms(mcId: string) {
      return !!this.acceptedByUser[mcId]
    },
  },
  persist: true,
})
