<script setup lang="ts">
import {
  MODAL_ACTIONS,
  type ModalActionType,
  type MyModalProps,
} from '~/lib/models/internal/ui/modal'

const props = withDefaults(defineProps<MyModalProps>(), {
  isOpen: false,
  illustration: '',
  title: '',
  message: '',
  htmlDescription: '',
  highlightInfo: undefined,
  actions: () => ({
    primary: undefined,
    secondary: undefined,
  }),
  isRedeemed: false,
})

const emit = defineEmits<{ close: []; action: [ModalActionType]; copied: [string] }>()

const handleActions = (type: ModalActionType) => emit('action', type)

const anyActions = computed(() => !!Object.values(props.actions).some(action => action))

const actionsClass = computed(() =>
  anyActions.value
    ? 'mt-8 flex flex-row items-center justify-between'
    : 'mt-8 flex flex-row items-center justify-center',
)
</script>
<template>
  <Transition
    enter-active-class="transition-all duration-300 ease-out"
    enter-from-class="opacity-0 translate-y-6"
    enter-to-class="opacity-100 translate-y-0"
    leave-active-class="transition-all duration-200 ease-in"
    leave-from-class="opacity-100 translate-y-0"
    leave-to-class="opacity-0 translate-y-6"
  >
    <section v-if="isOpen" class="mcd-game-overlay">
      <UIcon
        name="i-lucide-x"
        class="absolute top-6 left-4 z-50 text-2xl text-white"
        @click="emit('close')"
      />
      <div class="relative z-20 flex w-full flex-col items-center justify-end">
        <LoadImage
          class="relative bottom-[-32px] h-auto w-60"
          :url="illustration"
          :alt="`Image ${title}`"
        />
        <div class="flex min-h-[330px] w-full flex-col items-center justify-end bg-white">
          <!-- Body -->
          <div
            class="flex w-full flex-1 flex-col items-center justify-center p-4 pt-12 text-center"
          >
            <h2 v-if="title" class="text-lg font-bold">{{ title }}</h2>
            <p v-if="message" class="text-md">{{ message }}</p>
            <!-- Highlight section -->
            <div v-if="highlightInfo?.title || highlightInfo?.subtitle" class="mt-4">
              <div class="flex items-center justify-center gap-2">
                <p
                  class="text-2xl font-bold uppercase"
                  :class="{ 'text-mcd-darkGrey': isRedeemed }"
                  data-clipboard="code"
                >
                  {{ highlightInfo.title }}
                </p>
                <CopyToClipboard
                  v-if="!isRedeemed"
                  data-clipboard="code"
                  @copied="(text: string) => emit('copied', text)"
                />
              </div>
              <p class="mx-auto mt-1.5 w-3/4 text-xs">{{ highlightInfo.subtitle }}</p>
            </div>
            <!-- HTML Content (e.g. terms or additional text from backend) -->
            <div
              v-if="htmlDescription"
              v-dompurify-html="htmlDescription"
              class="mt-4 w-full text-left text-[8px]"
            />
            <!-- Content -->
            <slot name="content" />
          </div>
          <!-- Buttons -->
          <div v-if="anyActions" :class="actionsClass" class="w-full">
            <UButton
              v-if="actions.secondary"
              color="gray"
              class="h-16 rounded-none border-0 border-t"
              :class="{ 'w-full': anyActions }"
              :label="actions.secondary"
              @click="handleActions(MODAL_ACTIONS.SECONDARY)"
            />
            <UButton
              v-if="actions.primary"
              color="primary"
              class="h-16 rounded-none border-0 border-t"
              :class="{ 'w-full': anyActions }"
              :label="actions.primary"
              @click="handleActions(MODAL_ACTIONS.PRIMARY)"
            />
          </div>
        </div>
      </div>
    </section>
  </Transition>
</template>
