import { DateTime } from 'luxon'
import {
  BRIDGE_ACTIONS,
  type BridgeAction,
  type CurrentPrize,
  type KmpMessage,
} from '~/lib/models/kmp-bridge'

export const useKmpBridge = () => {
  const debug = useDebug('bridge')

  const isClient = typeof window !== 'undefined'
  const isIOS = isClient && !!window.webkit?.messageHandlers?.kmpJsBridge
  const isAndroid = isClient && !!window.kmpJsBridge && !isIOS

  const gameId = ref<string>('')
  const userId = ref<string>('')
  const isRedeemSuccess = ref<boolean>(false)
  const dataBridge = ref<Partial<KmpMessage>>({})
  const bridgeInitialized = ref(false)
  const isErrorBidgeConextion = ref(false)

  const callBridge = (action: BridgeAction, extra: Partial<KmpMessage> = {}) => {
    const message: KmpMessage = {
      action,
      gameId: gameId.value,
      userId: userId.value,
      score: '',
      claimResponse: undefined,
      startResponse: undefined,
      ...extra,
    }
    const messageString = JSON.stringify(message)
    debug('Sending message ', action, message)

    if (!isClient) {
      debug('KMP Bridge not available (no client)')
      return
    }

    // --- iOS bridge ---
    if (isIOS && window.webkit?.messageHandlers?.kmpJsBridge) {
      try {
        window.webkit.messageHandlers.kmpJsBridge.postMessage({
          handler: 'GameBridge',
          body: messageString,
        })
      } catch (err) {
        debug('Failed to post message to iOS bridge:', err)
      }
      return
    }

    // --- Android bridge ---
    if (isAndroid && typeof window.kmpJsBridge?.callNative === 'function') {
      window.kmpJsBridge.callNative('GameBridge', messageString, (response: string) => {
        debug(`Native response from Kotlin (${action}):`, response)
      })
      return
    }
    debug('[Bridge] Bridge not available')
    isErrorBidgeConextion.value = true
  }

  // --- Actions ---

  const startGame = () => callBridge(BRIDGE_ACTIONS.START_GAME)

  const endGame = () => callBridge(BRIDGE_ACTIONS.END_GAME, { score: '500' })

  const closeGame = () => callBridge(BRIDGE_ACTIONS.CLOSE_GAME)

  const copyCodeToClipboard = () => callBridge(BRIDGE_ACTIONS.COPY_GAME)

  // --- Handler for incoming Kotlin messages ---
  const handleKmpData = (data: string | KmpMessage) => {
    try {
      const dataParsed = typeof data === 'string' ? JSON.parse(data) : data
      debug('Received data from Kotlin:', dataParsed)
      console.log('dataParsed ------>')
      console.log(dataParsed)
      if (dataParsed.action === BRIDGE_ACTIONS.CLAIM_GAME_FROM_KT) {
        dataBridge.value.claimResponse = dataParsed.claimResponse
        // Redeem success
        const isWinner = !!dataBridge.value.claimResponse?.winner
        isRedeemSuccess.value = isWinner
        // Append the redeemed prize to the user's prize history on successful claim
        const currentPrize = dataBridge.value.startCurrent
        if (isWinner && currentPrize) {
          addRedeemedPrizeToHistory(currentPrize)
        }
      }
      if (dataParsed.action === BRIDGE_ACTIONS.START_GAME_FROM_KT) {
        dataBridge.value.startResponse = dataParsed.startResponse
        dataBridge.value.startCurrent = dataParsed.startCurrent
        dataBridge.value.startData = dataParsed.startData
        const dataPrizes = dataBridge.value.startResponse ?? {
          gameId: '',
          mcId: '',
        }
        gameId.value = dataPrizes.gameId
        userId.value = dataPrizes.mcId
        // End game claim
        endGame()
      }
      debug('Kotlin message processed successfully')
    } catch (error) {
      debug('Failed to parse Kotlin message:', error)
      return
    }
  }

  // --- Bridge initialization ---
  const initGame = () => {
    if (!isClient) return
    if (bridgeInitialized.value) return

    bridgeInitialized.value = true

    // Android (Kotlin)
    window.receiveDataFromKMP = handleKmpData

    // iOS (Swift)
    window.receiveDataFromNative = handleKmpData

    debug('KMP Bridge initialized for', isIOS ? 'iOS' : isAndroid ? 'Android' : 'Mock')

    startGame()
  }

  const addRedeemedPrizeToHistory = (currentPrize: CurrentPrize) => {
    const redeemedPrize = {
      identifierToken: currentPrize?.identifierToken,
      used: true,
      value: currentPrize?.code,
      type: currentPrize?.type,
      promotionDetails: currentPrize?.promotionDetails,
    }
    const newPrize = {
      date: DateTime.now().toFormat('yyyy-MM-dd'),
      items: [redeemedPrize],
    }
    const prizes = dataBridge.value.startResponse?.prizes ?? []
    if (dataBridge.value.startResponse && dataBridge.value.startResponse.prizes) {
      dataBridge.value.startResponse.prizes = [...prizes, newPrize]
    }
  }

  return {
    callBridge,
    initGame,
    startGame,
    endGame,
    closeGame,
    copyCodeToClipboard,
    dataBridge,
    isRedeemSuccess,
    isErrorBidgeConextion,
  }
}
