<script setup lang="ts">
import type { Link } from '~/lib/models/internal/ui/link'

const { t } = useTranslations()

const emit = defineEmits<{
  (e: 'accept'): void
}>()

const slides = [
  {
    img: '/images/advent-calendar/christmas-tree.svg',
    alt: 'Christmas tree',
    title: t('adventCalendar.intro.welcome.title'),
    desc: t('adventCalendar.intro.welcome.description'),
  },
  {
    img: '/images/advent-calendar/reveal-prize.svg',
    alt: 'Reveal Prize',
    title: t('adventCalendar.intro.revealPrize.title'),
    desc: t('adventCalendar.intro.revealPrize.description'),
  },
  {
    img: '/images/advent-calendar/prize-claimed.svg',
    alt: 'Prize Claimed',
    title: t('adventCalendar.intro.prizeClaimed.title'),
    desc: t('adventCalendar.intro.prizeClaimed.description'),
  },
  {
    img: '/images/advent-calendar/mcdonalds-prize.svg',
    alt: 'McDonalds Prize',
    title: t('adventCalendar.intro.acceptTerms.title'),
    desc: t('adventCalendar.intro.acceptTerms.description'),
    hasTerms: true,
  },
]

const linkTerms: Link = {
  title: t('adventCalendar.legal.terms.label'),
  blank: true,
  download: false,
  url: t('adventCalendar.legal.terms.link'),
}

const linkPrivacy: Link = {
  title: t('adventCalendar.legal.privacyPolicy.label'),
  blank: true,
  download: false,
  url: t('adventCalendar.legal.privacyPolicy.link'),
}

const form = reactive({
  terms: false,
})

const onSubmit = () => {
  if (form.terms) {
    emit('accept')
  }
}
</script>

<template>
  <section class="flex h-screen w-full">
    <UCarousel
      :items="slides"
      dots
      class="h-screen w-full"
      :ui="{ item: 'flex-shrink-0 w-full h-screen flex justify-center items-center' }"
    >
      <template #default="{ item }">
        <div class="px-5 text-center">
          <img
            :src="item.img"
            :alt="item.alt"
            class="mx-auto mb-10 block"
            :class="[{ 'mb-[120px]': item.hasTerms }]"
          />
          <h2 class="text-2xl font-bold">{{ item.title }}</h2>
          <p class="text-mcd-secondaryDarkGrey my-4 px-6 text-sm">{{ item.desc }}</p>
          <!-- Slide with terms-->
          <template v-if="item.hasTerms">
            <UForm :state="form" @submit="onSubmit">
              <div class="mt-12 flex flex-row items-start gap-4">
                <UFormField name="terms">
                  <UCheckbox v-model="form.terms" required />
                </UFormField>
                <p class="text-mcd-secondaryDarkGrey text-left text-sm">
                  <span>{{ t('adventCalendar.legal.common.accept') }}&nbsp;</span>
                  <ULink :to="linkTerms.url">{{ linkTerms.title }}</ULink>
                  <span>&nbsp;{{ t('adventCalendar.legal.common.and') }}&nbsp;</span>
                  <ULink :to="linkPrivacy.url">{{ linkPrivacy.title }}</ULink>
                </p>
              </div>
              <UButton
                type="submit"
                class="my-8 md:m-0"
                color="primary"
                block
                :disabled="!form.terms"
                :label="t('adventCalendar.actions.calendar')"
              />
            </UForm>
          </template>
        </div>
      </template>
    </UCarousel>
  </section>
</template>
