import * as z from 'zod'

export const BRIDGE_ACTIONS = {
  START_GAME: 'startGame_from_JS',
  CLOSE_GAME: 'closeGame_from_JS',
  END_GAME: 'endGame_from_JS',
  CLAIM_GAME_FROM_KT: 'claimGame_from_KT',
  START_GAME_FROM_KT: 'startGame_from_KT',
  COPY_GAME: 'copyGame_from_JS',
} as const

export const KmpActionSchema = z.enum(Object.values(BRIDGE_ACTIONS) as [string, ...string[]])

export type BridgeAction = z.infer<typeof KmpActionSchema>

export interface KmpJsBridge {
  callNative<T = unknown>(
    bridgeName: string,
    message: string,
    callback: (response: string | T) => void,
  ): void
}

const ClaimResponseSchema = z.object({
  code: z.string().optional(),
  winner: z.boolean().optional(),
  loyaltyPoints: z.number().optional(),
})

const PromotionDetailsSchema = z.object({
  name: z.string().nullable(),
  description: z.string().nullable(),
  dateTo: z.string().nullable(),
  imageUrl: z.string().nullable(),
  url: z.string().nullable(),
  termsAndConditions: z.string().nullable(),
})

const currentPrizeSchema = z.object({
  identifierToken: z.string(),
  gameId: z.string(),
  code: z.string(),
  active: z.boolean(),
  countryCode: z.string(),
  amount: z.number(),
  mcId: z.string(),
  refPromo: z.string(),
  type: z.string(),
  promotionDetails: PromotionDetailsSchema.optional().nullable(),
})

export type CurrentPrize = z.infer<typeof currentPrizeSchema>

const ItemPrizeSchema = z.object({
  identifierToken: z.string().optional().nullable(),
  used: z.boolean().optional().nullable(),
  value: z.string().optional().nullable(),
  type: z.string().optional().nullable(),
  promotionId: z.string().optional().nullable(),
  promotionDetails: PromotionDetailsSchema.optional().nullable(),
})

const HistoricPrizeSchema = z.object({
  date: z.string(),
  items: z.array(ItemPrizeSchema),
})

const HistoricPrizesSchema = z.object({
  mcId: z.string(),
  gameId: z.string(),
  prizes: z.array(HistoricPrizeSchema),
})

const BaseMessageSchema = z.object({
  action: KmpActionSchema,
  gameId: z.string(),
})

/**
 * PrizeData
 */
export const PrizeDataSchema = z.object({
  label: z.string().nullable().optional(),
  value: z.string().nullable().optional(),
  type: z.string().nullable().optional(),
})

export const KmpMessageSchema = BaseMessageSchema.extend({
  userId: z.string().optional(),
  score: z.string().optional(),
  claimResponse: ClaimResponseSchema.optional().nullable(),
  startResponse: HistoricPrizesSchema.optional().nullable(),
  startData: z.array(PrizeDataSchema).optional().nullable(),
  startCurrent: currentPrizeSchema.optional(),
})

export type KmpMessage = z.infer<typeof KmpMessageSchema>
